<template>
    <div>
        <!-- <el-button @click="getSongId()">getSong</el-button> -->
        <audio :src="songUrl" controls></audio>
    </div>
</template>

<script>
export default {
    data() {
        return {
            songUrl:""
        }
    },
    methods:{
        getSongId(){
            //personalized?limit=1
            this.axios.get('/personalized?limit=1').
            then((res) => {
                this.getSong(res.data.result[0].id);
            }).catch((err) => {
                console.log(err)    
            });
        },

        getSong(id){
            this.songUrl = 'https://music.163.com/song/media/outer/url?id='+id+'.mp3';
        }
    }
}
</script>

<style scoped>
    div{
        position: fixed;
        height: 9vh;
        bottom: 0;
        width: 95%;
        min-width: 365px;
        max-width: 365px;
        border: 1px solid orange;
    }
</style>